<script>

export default class PayplugStandardWarningMessage{

	msg = null;
	msg_title = null;
	name = null;
	type = "warning_msg";
	payment_method = null;

	constructor(data)
	{
		this.name = data.name;
		this.msg = data.description;
		this.payment_method = data.payment_method;
		this.msg_title = data.description_title;
	}

}

</script>

