<template>
	<slot
		v-if=" paymentOption.type ==='warning_msg' &&
		( paymentOption.payment_method != 'integrated' ||  paymentOption.payment_method === store.data.payplug_embeded )
		"
	>
		<textAlert alertType="warning"  :alertTitle="paymentOption.msg_title"  :alertContent="paymentOption.msg" alertIcon="lightbulb_orange"  />
	</slot>
	<slot v-else-if="paymentOption.type !=='input'">
		<div class="paymentMethodOption">
			<PMethodHeader :description="descriptionComputed ?? this.description" :title="paymentOption.title"  :links="links" class="" />
			<div class="_action">
				<payplugUIIOptions
					v-if="paymentOption.name == 'embeded'"
					class="_sandboxRadioButton"
					:optionsName="'payplug_' + paymentOption.name"
					:optionsItems="paymentOption.options"
					optionsData="embeddedMode"
					@update_embeded_option="handleEmbededOptionTranslation"
				/>
				<Switch
				v-if="paymentOption.name == 'one_click' || paymentOption.name == 'bancontact_country'"
				:switchName="paymentOption.name"
				:switchChecked="paymentOption.checked ? true : null"
				:switchData="paymentMethod.name == 'bancontact' ? 'bancontactCountrySwitch' : 'oneClickSwitch'"
				/>
			</div>
		</div>
	</slot>
	<slot v-else>
		<AtomInputForm :paymentOption="this.paymentOption" />
	</slot>
</template>

<script>
import Switch from "../../atoms/AtomSwitch";
import PMethodHeader from "@/components/organisms/PMethod/OrganismPMethodHeader";
import payplugUIIOptions from "@/components/atoms/AtomOptions";
import {store} from "@/store/store";
import AtomInputForm from "@/components/atoms/AtomInputForm";
import textAlert from  "@/components/molecules/MoleculeAlert";

export default {
	name: "OrganismPMethodOption",
	components:{AtomInputForm, payplugUIIOptions, PMethodHeader, Switch, textAlert },
	computed:{
		descriptionComputed:{
			get() {
				return store.data.payplug_embeded ? this.paymentOption["description_"+store.data.payplug_embeded] : this.description;
			}
		}
	},
	data() {
		return {
			store: store,
		}
	},
	props: {
		paymentMethod:{
			type: Object,
			required: true
		},
		paymentOption:{
			type: Object,
			required: true
		}
	},
	created() {
		this.init()
	},
	updated() {
		this.init()
	},
	methods: {
		init(){
			this.description = this.paymentOption.description;
			this.links = this.paymentOption.link_know_more ?? null;
		},
		handleEmbededOptionTranslation(value){
			this.description = this.paymentOption["description_"+value];
		}
	}
}
</script>

