<template>
  <p class="payplugUIParagraph">
    <span v-html="paragraphAnchorsText"></span>

	<slot v-if="info_available && paragraphAnchorsTextInfo != null">
		<SpanInfo :infoDescription="paragraphAnchorsTextInfo"></SpanInfo>
	</slot>

    <template v-if="paragraphAnchorsLinks">
      <template v-for="link in paragraphAnchorsLinks">
        <Anchor v-if="link"
                :key="link"
                :anchorHref="link?.url"
                :anchorText="link?.text"
                :anchorTarget="link?.target"
                :data-e2e-name="paragraphAnchorsData"/>
      </template>
    </template>
  </p>
</template>

<script>
import Anchor from "../atoms/AtomAnchor";
import SpanInfo from "../atoms/AtomSpanInfo";
import {store} from "@/store/store";

export default {
  name: "MoleculeParagraphAnchors",
  components: {Anchor, SpanInfo},
	data(){
		return {
			store:store
		}
	},
	computed:{
		info_available:{
			get() {
				return this.store.data['enable_' + this.paymentMethod];
			}
		}
	},
	props: {
		paymentMethod:{
			Type: String,
			Required: true
		},
		paragraphAnchorsText: {
			Type: String,
			Required: false
		},
		paragraphAnchorsTextInfo: {
			Type: String,
			Required: false
		},
		paragraphAnchorsLinks: {
			Type: Object,
			Required: false
		},
		paragraphAnchorsData: {
			Type: String,
			Required: false
		}
	}
}
</script>

